@import url('https://fonts.googleapis.com/css?family=Audiowide');


body {
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.pricing {
    width: 100%;
    height: 100%;
}

.pricing-header {
    width: 100%;
    text-align: center;
    color: white;
}

.pricing-div {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: auto;
}

.bronze {
    width: auto;
    height: auto;
    background-color: rgba(205, 127, 50, .33);
    border-radius: .75vh;
}

.silver {
    width: auto;
    height: auto;
    background-color: rgba(170, 169, 173, .33);
    border-radius: .75vh;
}

.gold {
    width: auto;
    height: auto;
    background-color: rgba(255, 215, 0, .33);
    border-radius: .75vh;
}

.bottom-div {
    position: relative;
    margin-top: 5%;
    width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: space-evenly;
}

.bottom-div a{
    color: #7a7f87;
    text-decoration: underline;
    font-size: 2.5vh;
}

.bottom-div a:hover {
    color: #0984e3;
    cursor: pointer;
}

@media (max-width: 800px) {
    .pricing-div {
        display: inline-block;

    }

    .bronze, .silver, .gold {
        width: 300px;
        margin: auto;
        margin-bottom: 2vh;
    }

    .bottom-div {
        width: 100%;
        text-align: center;
        margin-bottom: 1vh;
    } 
}